	module.exports = {"data":{"markdownRemark":{"html":"<h1>For The Sake Of The Song</h1>\n<p>Cover songs are a great tool.\nThey are great to mix into a live set.\nThey also provide a medium to practice arrangement without the pressures associated with arranging/performing original work.</p>\n<p>Since I began playing guitar I have use covers as a learning tool.\nThe tool still serves me well.\nI enjoy playing and arranging covers, and I constantly learn from the practice.</p>\n<p>In December 2015, I executed a rigorous cover song project called <a href=\"https://soundcloud.com/richsoni/sets/my-dylcember\">Dylcember (check it out here)</a>.\nThe premise was to record and post 31 Bob Dylan Cover Songs in the month (one track per day).\nEven with my strong knowledge of Bob Dylan's song catalog, arranging and recording a cover per day was ambitious.\nEspecially having a 3 hour commute to a full time job, and family to spend time with during the holiday season.\nThis challenge was tough, but I successfully completed it.</p>\n<p>The rapid pace strained my abilities to their max.\nI was fatigued and working with no time or energy to spare.\nYet, I still had a desire to produce something I was proud of.\nThrough this dedication, strain, and repetition my skills evolved.</p>\n<p>I came up with this framework for writing covers.\nTargeted consistency prepares the creative mind with minimal strain.\nNot only is it the methodology quick, but it produces high quality covers.\nIt is very efficient.</p>\n<p>Lets get to it!</p>\n<h1>The Easiest Way To Learn A Cover</h1>\n<p>Here are the steps I followed each night:</p>\n<ol>\n<li>Arrangement: Learn The Chord Changes</li>\n<li>Arrangement: Learn Some Lyrics From Each Section</li>\n<li>Arrangement: Learn Some Of The Melody Notes</li>\n<li>Arrangement: Take A Break</li>\n<li>Arrangement: Find Your Key</li>\n<li>Arrangement: Find Your Chords / Strum Pattern</li>\n<li>Delivery: Learn All The Lyrics</li>\n<li>Delivery: Discover Your Delivery</li>\n<li>Delivery: Capture The Performance For Future Reference</li>\n</ol>\n<p>The next sections provide some advice/explanation for each step.</p>\n<h2>Learn The Chord Changes</h2>\n<p>The goal here is to play along with the song without written instruction.\nPull up chords online, or learn them by ear.\nIf there are multiple sections, learn them all.</p>\n<h2>Learn Lyrics One Of Each Section</h2>\n<p>I can accomplish this in tandem with learning chords most of the time.\nThe main reason for this step is to allow you to step away from the computer.\nIt is also a general distraction to fuss over the lyrics while arranging.\nYou have to learn them anyway, so just get started now.</p>\n<h2>Learn Some Of The Melody Notes</h2>\n<p>I prefer to let my brain find the key for me.\nTo do this, you first need to know a few notes in the original melody.</p>\n<p>Find a spot in the song, the beginning or a strong part and figure out the notes on guitar in the original key.\nDon't go crazy, just learn a few reference notes.</p>\n<h2>Take A Break</h2>\n<p>Get the song out of your head.\nIts important for the next step.</p>\n<h2>Find Your Key</h2>\n<p>A little later start singing that part acapella, and then grab your guitar.\nFind the note you are singing on guitar, and adjust your capo to match the chords of that part.\nYou should know the relationship because you found the melody note earlier.</p>\n<h2>Find Your Chords / Strum Pattern</h2>\n<p>This is a very important yet often neglected part.\nFind the chords that sound the best.\nJust because you found the key by moving the capo up 3 frets does not mean you should play it that way.</p>\n<p>Maybe this song sounds better with no capo, but instead a D voicing in standard tuning, maybe its cool in open D, maybe its nice with the capo on 2 with the C shape.\nTry them all out, and try stumming patterns/tempo/rhythems with them all as well.\nThis is the artistic part of the arrangement right here.\nMake it you, make it new, make it interesting.</p>\n<p>I have discovered many awesome chord voicings from this part of the process.\nNothing crazy, here are a few I have never encountered before:</p>\n<pre><code>  A D E\nE 0 5 0\nB 5 7 0\nG 6 7 9\nD 7 0 9\nA 0 x 7\nE x x 0\n</code></pre>\n<h2>Learn All The Lyrics</h2>\n<p>This is self explanatory.\nI usually memorize each verse in isolation repeating a part 4x without mistakes, then adding a new line.\nThen do that verse and the one before it 4x without mistakes.</p>\n<p>You cannot deliver effectively without memorizing the lyrics.\nTake the time to do it.</p>\n<h2>Discover Your Delivery</h2>\n<p>Now you know all the words, and the way you want to play the song.\nFind words you want to savor, and practice their delivery.\nYou are pretty much done.\nAlthough a song is never learned, delivery can always improve.</p>\n<h2>Record It For Future Reference</h2>\n<p>Even if you do not play in releasing a recording or a video, make sure you get one for your own reference.\nAlso I recommend noting the instrumentation because you may forget it later.\nTo kill two birds with one stone, I usually PhotoBooth the performance with the guitar in frame.</p>","frontmatter":{"title":"A Recipe For Arranging/Learning Impressive Cover Songs"},"fields":{"date":"2016-01-19"}}},"pathContext":{"url":"/posts/2016-01-19-a-recipe-for-arranging-learning-impressive-cover-songs/"}}

